/* 기본 스타일 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'STUNNING-Bd', sans-serif; /* 여기서 OTF 폰트를 적용 */
  background-color: #f4f4f4;
  color: #333;
  line-height: 1.6;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.navbar {
  background-color: #4e54c8;
  color: white;
  padding: 15px;
  text-align: center;
}

.navbar-menu {
  display: flex;
  justify-content: center;
  gap: 20px;
  list-style-type: none;
}

.navbar-menu li {
  font-size: 1rem;
}

.navbar-menu a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

.navbar-menu a:hover {
  text-decoration: underline;
}

/* 프로필 섹션 */
.profile-container {
  display: flex;
  justify-content: flex-start;
  gap: 20px;
  align-items: center;
  margin-top: 40px;
}

.profile-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.about-text {
  max-width: 500px;
}

.about-text h1 {
  font-size: 1.6em;
}

.about-text p {
  font-size: 1em;
}

/* 기술 스택 섹션 */
.tech-stack {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.tech-stack-item {
  background-color: #f0f0f0;
  color: #333;
  padding: 8px 15px;
  border-radius: 12px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  font-size: 1em;
  transition: background-color 0.3s ease, transform 0.3s ease; /* 부드러운 효과 추가 */
}

.tech-stack-item:hover {
  background-color: #4e54c8;
  color: white;
  transform: scale(1.1);
}

/* 반응형 디자인: 모바일 화면 */
@media (max-width: 768px) {
  .navbar-menu {
    flex-direction: column;
    align-items: center;
  }

  .profile-container {
    flex-direction: column;
    align-items: center;
  }

  .profile-image {
    width: 80px;
    height: 80px;
  }

  .about-text h1 {
    font-size: 1.4em;
  }

  .about-text p {
    font-size: 0.9em;
  }

  .tech-stack-item {
    font-size: 1.2em; /* 모바일에서 글씨 크기 증가 */
  }
}

/* 아주 작은 화면 (모바일) */
@media (max-width: 480px) {
  .profile-image {
    width: 60px;
    height: 60px;
  }

  .about-text h1 {
    font-size: 1.2em;
  }

  .about-text p {
    font-size: 0.85em;
  }

  .navbar-menu li {
    font-size: 0.8rem;
  }

  .navbar {
    padding: 10px;
  }
}
