/* 기본 메뉴 스타일 */
.navbar-menu ul {
  list-style-type: none;
  display: flex;
  gap: 20px;
  justify-content: flex-start;
}

.navbar-menu li {
  font-size: 1.1rem;
}

.navbar-menu a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

.navbar-menu a:hover {
  text-decoration: underline;
}

/* 모바일에서 메뉴가 세로로 나타나도록 */
@media (max-width: 768px) {
  .navbar-menu ul {
    flex-direction: column;
    align-items: center;
  }
}
